/**
 * uni.xxx
 * 
 */

class Router {
  constructor() {
    // 
    this.config = {
      type: 'navigateTo',
      url: '',
      delta: 1, // navigateBack,
      params: {}, // 
      animationType: 'pop-in', // ,APP
      animationDuration: 300, // ,,APP
      intercept: false, // 
    }
    // routeroutethisroute
    // this
    this.route = this.route.bind(this)
  }

  // url"/"
  addRootPath(url) {
    return url[0] === '/' ? url : `/${url}`
  }

  // 
  mixinParam(url, params) {
    url = url && this.addRootPath(url)

    // "/","?","="/page/index/index?name=mary"
    // urlget"?"
    let query = ''
    if (/.*\/.*\?.*=.*/.test(url)) {
      // objectget
      query = uni.$u.queryParams(params, false)
      // get,"&"
      return (url += `&${query}`)
    }
    // urlquery"?/&"
    query = uni.$u.queryParams(params)
    return (url += query)
  }

  // 
  async route(options = {}, params = {}) {
    // 
    let mergeConfig = {}

    if (typeof options === 'string') {
      // optionsroute(url, params)
      mergeConfig.url = this.mixinParam(options, params)
      mergeConfig.type = 'navigateTo'
    } else {
      mergeConfig = uni.$u.deepClone(options, this.config)
      // mergeConfigurlparams
      mergeConfig.url = this.mixinParam(options.url, options.params)
    }

    // 
    if (mergeConfig.url === uni.$u.page()) return

    if (params.intercept) {
      this.config.intercept = params.intercept
    }
    // params
    mergeConfig.params = params
    // 
    mergeConfig = uni.$u.deepMerge(this.config, mergeConfig)
    // 
    if (typeof uni.$u.routeIntercept === 'function') {
      // promiseresolve(true)resolve(false)
      const isNext = await new Promise((resolve, reject) => {
        uni.$u.routeIntercept(mergeConfig, resolve)
      })
      // isNexttrue
      isNext && this.openPage(mergeConfig)
    } else {
      this.openPage(mergeConfig)
    }
  }

  // 
  openPage(config) {
    // 
    const { url, type, delta, animationType, animationDuration } = config
    if (config.type == 'navigateTo' || config.type == 'to') {
      uni.navigateTo({
        url,
        animationType,
        animationDuration,
      })
    }
    if (config.type == 'redirectTo' || config.type == 'redirect') {
      uni.redirectTo({
        url,
      })
    }
    if (config.type == 'switchTab' || config.type == 'tab') {
      uni.switchTab({
        url,
      })
    }
    if (config.type == 'reLaunch' || config.type == 'launch') {
      uni.reLaunch({
        url,
      })
    }
    if (config.type == 'navigateBack' || config.type == 'back') {
      uni.navigateBack({
        delta,
      })
    }
  }
}

export default new Router().route
