import { HttpService } from "./http.service";
//  HttpService
export default new HttpService({
  timeout: 6 * 1000,
  interceptor: {
    // 
    request(config) {
      config.header.Authorization = uni.getStorageSync('token')
      return config
    },
    // 
    response(event) {
      // 
      if (!event.success) {
        uni.showToast({
          title: event.errMsg,
          icon: 'none'
        });
        //  catch 
        return event;
      }
      // 
      const {
        statusCode,
        data: {
          code,
          data,
          msg = '',
        }
      } = event;
      switch (statusCode) {
        case 200:
          switch (code) {
            case 200:
              return Promise.resolve(data);
            default:
              uni.showToast({
                title: msg,
                icon: 'none'
              });
          }
          return Promise.reject(msg);
        case 401:
          uni.showToast({
            title: 'token ',
            icon: 'none'
          });
          // other code
          break;
        default:
          uni.showToast({
            title: `response exception: ${statusCode}`,
            icon: 'none'
          });
          return Promise.reject(event);
      }
    }
  }
})