import test from './test.js'

/**
 * valueminminvaluemaxmax
 */
function range(min = 0, max = 0, value = 0) {
  return Math.max(min, Math.min(max, Number(value)))
}

/**
 * px
 * "xxpx""xxrpx""xxxrpx"uni.upx2px
 */
function getPx(value, unit = false) {
  if (test.number(value)) {
    return unit ? `${value}px` : value
  }
  // rpxpx
  if (/(rpx|upx)$/.test(value)) {
    return unit ? `${uni.upx2px(parseInt(value))}px` : uni.upx2px(parseInt(value))
  }
  return unit ? `${parseInt(value)}px` : parseInt(value)
}

/**
 * 
 * await uni.$u.sleep(20)20ms
 */
function sleep(value = 30) {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve()
    }, value)
  })
}

function os() {
  return uni.getSystemInfoSync().platform.toLowerCase()
}

function sys() {
  return uni.getSystemInfoSync()
}

/**
 * 
 * @param {Number} min 
 * @param {Number} max 
 */
function random(min, max) {
  if (min >= 0 && max > 0 && max >= min) {
    const gab = max - min + 1
    return Math.floor(Math.random() * gab + min)
  }
  return 0
}

/**
 * https://www.jianshu.com/p/fdbf293d0a85
 * uuidGlobally Unique Identifier, uuid(Universally Unique IDentifier)
 * ,,v-for,index
 * item"",
 * v-for,idindex
 * @param {Number} len uuid
 * @param {Boolean} firstU "u"
 * @param {Nubmer} radix uuid(),2-,8-,10-,16-
 */
function guid(len = 32, firstU = true, radix = null) {
  const chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('')
  const uuid = []
  radix = radix || chars.length

  if (len) {
    // uuid,,0|x,x,
    for (let i = 0; i < len; i++) uuid[i] = chars[0 | (Math.random() * radix)]
  } else {
    let r
    // rfc4122uuid,
    uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-'
    uuid[14] = '4'

    for (let i = 0; i < 36; i++) {
      if (!uuid[i]) {
        r = 0 | (Math.random() * 16)
        uuid[i] = chars[i == 19 ? (r & 0x3) | 0x8 : r]
      }
    }
  }
  // ,u,,guuididclass
  if (firstU) {
    uuid.shift()
    return `u${uuid.join('')}`
  }
  return uuid.join('')
}

// provide/inject
// this.$parentH5H5this.$parent.$parent.xxx
// undefined()$parentundefinedname
// (undefined)$parent
function $parent(name = undefined) {
  let parent = this.$parent
  // whileH5
  while (parent) {
    // 
    if (parent.$options && parent.$options.name !== name) {
      // name
      parent = parent.$parent
    } else {
      return parent
    }
  }
  return false
}

/**
 * 
 * 
 * @param {Object | String} 
 * @param {String} object-string-
 */
function addStyle(customStyle, target = 'object') {
  // 
  if (
    test.empty(customStyle) ||
    (typeof customStyle === 'object' && target === 'object') ||
    (target === 'string' && typeof customStyle === 'string')
  ) {
    return customStyle
  }
  // 
  if (target === 'object') {
    // (padding: 20px 0)
    customStyle = trim(customStyle)
    // ";"
    const styleArray = customStyle.split(';')
    const style = {}
    // 
    for (let i = 0; i < styleArray.length; i++) {
      // 'font-size:20px;color:red;'";"styleArray
      if (styleArray[i]) {
        const item = styleArray[i].split(':')
        style[trim(item[0])] = trim(item[1])
      }
    }
    return style
  }
  // 
  let string = ''
  for (const i in customStyle) {
    // css
    const key = i.replace(/([A-Z])/g, '-$1').toLowerCase()
    string += `${key}:${customStyle[i]};`
  }
  // 
  return trim(string)
}

// rpxupx%pxautopx
function addUnit(value = 'auto', unit = 'px') {
  value = String(value)
  // uViewnumber
  return test.number(value) ? `${value}${unit}` : value
}

// 
function deepClone(obj) {
  // 
  if ([null, undefined, NaN, false].includes(obj)) return obj
  if (typeof obj !== 'object' && typeof obj !== 'function') {
    // 
    return obj
  }
  const o = test.array(obj) ? [] : {}
  for (const i in obj) {
    if (obj.hasOwnProperty(i)) {
      o[i] = typeof obj[i] === 'object' ? deepClone(obj[i]) : obj[i]
    }
  }
  return o
}

// JS
function deepMerge(target = {}, source = {}) {
  target = deepClone(target)
  if (typeof target !== 'object' || typeof source !== 'object') return false
  for (const prop in source) {
    if (!source.hasOwnProperty(prop)) continue
    if (prop in target) {
      if (typeof target[prop] !== 'object') {
        target[prop] = source[prop]
      } else if (typeof source[prop] !== 'object') {
        target[prop] = source[prop]
      } else if (target[prop].concat && source[prop].concat) {
        target[prop] = target[prop].concat(source[prop])
      } else {
        target[prop] = deepMerge(target[prop], source[prop])
      }
    } else {
      target[prop] = source[prop]
    }
  }
  return target
}

function error(err) {
  // 
  if (process.env.NODE_ENV === 'development') {
    console.error(`uView${err}`)
  }
}

// 
function randomArray(array = []) {
  // sort,Math.random()0<= x < 1,x-0.050
  return array.sort(() => Math.random() - 0.5)
}

// padStart  polyfilles7padStart
// polyfill
if (!String.prototype.padStart) {
  //  fillString ES6 
  String.prototype.padStart = function (maxLength, fillString = ' ') {
    if (Object.prototype.toString.call(fillString) !== '[object String]') {
      throw new TypeError('fillString must be String')
    }
    const str = this
    //  String(str) 
    if (str.length >= maxLength) return String(str)

    const fillLength = maxLength - str.length
    let times = Math.ceil(fillLength / fillString.length)
    while ((times >>= 1)) {
      fillString += fillString
      if (times === 1) {
        fillString += fillString
      }
    }
    return fillString.slice(0, fillLength) + str
  }
}

// :
// yyyy:mm:dd|yyyy:mm|yyyymmdd|yyyymmdd hhMM,
function timeFormat(dateTime = null, fmt = 'yyyy-mm-dd') {
  // null,
  if (!dateTime) dateTime = Number(new Date())
  // dateTime101313
  if (dateTime.toString().length == 10) dateTime *= 1000
  const date = new Date(dateTime)
  let ret
  const opt = {
    'y+': date.getFullYear().toString(), // 
    'm+': (date.getMonth() + 1).toString(), // 
    'd+': date.getDate().toString(), // 
    'h+': date.getHours().toString(), // 
    'M+': date.getMinutes().toString(), // 
    's+': date.getSeconds().toString(), // 
    // 
  }
  for (const k in opt) {
    ret = new RegExp(`(${k})`).exec(fmt)
    if (ret) {
      fmt = fmt.replace(ret[1], ret[1].length == 1 ? opt[k] : opt[k].padStart(ret[1].length, '0'))
    }
  }
  return fmt
}

/**
 * 
 * @param String timestamp 
 * @param String | Boolean format 
 * false
 */
function timeFrom(timestamp = null, format = 'yyyy-mm-dd') {
  if (timestamp == null) timestamp = Number(new Date())
  timestamp = parseInt(timestamp)
  // ,js(13),(10)
  if (timestamp.toString().length == 10) timestamp *= 1000
  let timer = new Date().getTime() - timestamp
  timer = parseInt(timer / 1000)
  // 5,"",
  let tips = ''
  switch (true) {
    case timer < 300:
      tips = ''
      break
    case timer >= 300 && timer < 3600:
      tips = `${parseInt(timer / 60)}`
      break
    case timer >= 3600 && timer < 86400:
      tips = `${parseInt(timer / 3600)}`
      break
    case timer >= 86400 && timer < 2592000:
      tips = `${parseInt(timer / 86400)}`
      break
    default:
      // formatfalsexx
      if (format === false) {
        if (timer >= 2592000 && timer < 365 * 86400) {
          tips = `${parseInt(timer / (86400 * 30))}`
        } else {
          tips = `${parseInt(timer / (86400 * 365))}`
        }
      } else {
        tips = timeFormat(timestamp, format)
      }
  }
  return tips
}

/**
 * 
 */
function trim(str, pos = 'both') {
  str = String(str)
  if (pos == 'both') {
    return str.replace(/^\s+|\s+$/g, '')
  }
  if (pos == 'left') {
    return str.replace(/^\s*/, '')
  }
  if (pos == 'right') {
    return str.replace(/(\s*$)/g, '')
  }
  if (pos == 'all') {
    return str.replace(/\s+/g, '')
  }
  return str
}

/**
 * url
 * @param {*} data,
 * @param {*} isPrefix,"?"
 */
function queryParams(data = {}, isPrefix = true, arrayFormat = 'brackets') {
  const prefix = isPrefix ? '?' : ''
  const _result = []
  if (['indices', 'brackets', 'repeat', 'comma'].indexOf(arrayFormat) == -1)
    arrayFormat = 'brackets'
  for (const key in data) {
    const value = data[key]
    // 
    if (['', undefined, null].indexOf(value) >= 0) {
      continue
    }
    // 
    if (value.constructor === Array) {
      // e.g. {ids: [1, 2, 3]}
      switch (arrayFormat) {
        case 'indices':
          // : ids[0]=1&ids[1]=2&ids[2]=3
          for (let i = 0; i < value.length; i++) {
            _result.push(`${key}[${i}]=${value[i]}`)
          }
          break
        case 'brackets':
          // : ids[]=1&ids[]=2&ids[]=3
          value.forEach((_value) => {
            _result.push(`${key}[]=${_value}`)
          })
          break
        case 'repeat':
          // : ids=1&ids=2&ids=3
          value.forEach((_value) => {
            _result.push(`${key}=${_value}`)
          })
          break
        case 'comma':
          // : ids=1,2,3
          let commaStr = ''
          value.forEach((_value) => {
            commaStr += (commaStr ? ',' : '') + _value
          })
          _result.push(`${key}=${commaStr}`)
          break
        default:
          value.forEach((_value) => {
            _result.push(`${key}[]=${_value}`)
          })
      }
    } else {
      _result.push(`${key}=${value}`)
    }
  }
  return _result.length ? prefix + _result.join('&') : ''
}

function toast(title, duration = 2000) {
  uni.showToast({
    title: String(title),
    icon: 'none',
    duration,
  })
}

/**
 * type,
 * @param String type ,primary|info|error|warning|success
 * @param String fill fill
 */
function type2icon(type = 'success', fill = false) {
  // ,success
  if (['primary', 'info', 'error', 'warning', 'success'].indexOf(type) == -1) type = 'success'
  let iconName = ''
  // (2019-12-12),infoprimary
  switch (type) {
    case 'primary':
      iconName = 'info-circle'
      break
    case 'info':
      iconName = 'info-circle'
      break
    case 'error':
      iconName = 'close-circle'
      break
    case 'warning':
      iconName = 'error-circle'
      break
    case 'success':
      iconName = 'checkmark-circle'
      break
    default:
      iconName = 'checkmark-circle'
  }
  // ,-fill,icon,-fill
  if (fill) iconName += '-fill'
  return iconName
}

/*
 * 
 * number
 * decimals
 * decimalPoint
 * thousandsSeparator
 * */
function priceFormat(number, decimals = 0, decimalPoint = '.', thousandsSeparator = ',') {
  number = `${number}`.replace(/[^0-9+-Ee.]/g, '')
  const n = !isFinite(+number) ? 0 : +number
  const prec = !isFinite(+decimals) ? 0 : Math.abs(decimals)
  const sep = typeof thousandsSeparator === 'undefined' ? ',' : thousandsSeparator
  const dec = typeof decimalPoint === 'undefined' ? '.' : decimalPoint
  let s = ''
  const toFixedFix = function (n, prec) {
    const k = 10 ** prec
    return `${Math.ceil(n * k) / k}`
  }

  s = (prec ? toFixedFix(n, prec) : `${Math.round(n)}`).split('.')
  const re = /(-?\d+)(\d{3})/
  while (re.test(s[0])) {
    s[0] = s[0].replace(re, `$1${sep}$2`)
  }

  if ((s[1] || '').length < prec) {
    s[1] = s[1] || ''
    s[1] += new Array(prec - s[1].length + 1).join('0')
  }
  return s.join(dec)
}

// durationmssmss
// 3030030300ms300s
function getDuration(value, unit = true) {
  const valueNum = parseInt(value)
  if (unit) {
    if (/s$/.test(value)) return value
    return value > 30 ? `${value}ms` : `${value}s`
  }
  if (/ms$/.test(value)) return valueNum
  if (/s$/.test(value)) return valueNum > 30 ? valueNum : valueNum * 1000
  return valueNum
}

// 
function padZero(value) {
  return `00${value}`.slice(-2)
}

// u-formu-form
function formValidate(instance, event) {
  const formItem = uni.$u.$parent.call(instance, 'u-form-item')
  const form = uni.$u.$parent.call(instance, 'u-form')
  // inputtextareau-form-itemu-formformvalidate
  // form-itemprosform
  if (formItem && form) {
    form.validateField(formItem.prop, () => {}, event)
  }
}

// 'a.b.c'
function getProperty(obj, key) {
  if (!obj) {
    return
  }
  if (typeof key !== 'string' || key === '') {
    return ''
  }
  if (key.indexOf('.') !== -1) {
    const keys = key.split('.')
    let firstObj = obj[keys[0]] || {}

    for (let i = 1; i < keys.length; i++) {
      if (firstObj) {
        firstObj = firstObj[keys[i]]
      }
    }
    return firstObj
  }
  return obj[key]
}

// 'a.b.c'
function setProperty(obj, key, value) {
  if (!obj) {
    return
  }
  // 
  const inFn = function (_obj, keys, v) {
    // key
    if (keys.length === 1) {
      _obj[keys[0]] = v
      return
    }
    // 0~length-1key
    while (keys.length > 1) {
      const k = keys[0]
      if (!_obj[k] || typeof _obj[k] !== 'object') {
        _obj[k] = {}
      }
      const key = keys.shift()
      // 
      inFn(_obj[k], keys, v)
    }
  }

  if (typeof key !== 'string' || key === '') {
  } else if (key.indexOf('.') !== -1) {
    // 
    const keys = key.split('.')
    inFn(obj, keys, value)
  } else {
    obj[key] = value
  }
}

// 
function page() {
  const pages = getCurrentPages()
  return `/${getCurrentPages()[pages.length - 1].route}`
}

export default {
  range,
  getPx,
  sleep,
  os,
  sys,
  random,
  guid,
  $parent,
  addStyle,
  addUnit,
  deepClone,
  deepMerge,
  error,
  randomArray,
  timeFormat,
  timeFrom,
  trim,
  queryParams,
  toast,
  type2icon,
  priceFormat,
  getDuration,
  padZero,
  formValidate,
  getProperty,
  setProperty,
  page,
}
